'use client';

import Link from 'next/link';
import { useSettings } from '@/hooks';
import { Instagram, Twitter, MessageCircle, Mail, Palette, Video, Share2, Linkedin, Globe } from 'lucide-react';

export default function Footer() {
    const { settings } = useSettings();

    const socialIcons: Record<string, any> = {
        Instagram,
        Twitter,
        Facebook: MessageCircle,
        Discord: MessageCircle,
        Email: Mail,
        ArtStation: Palette,
        DeviantArt: Palette,
        Behance: Palette,
        YouTube: Video,
        TikTok: Video,
        LinkedIn: Linkedin,
        Default: Globe,
    };

    return (
        <footer className="bg-[var(--bg-secondary)] py-12 border-t border-[var(--border-primary)] mt-auto">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-8 items-center">
                    {/* Brand */}
                    <div className="text-center md:text-left">
                        <p className="text-2xl font-bold text-[var(--text-primary)] font-accent">
                            {settings?.siteTitle || 'Miqk Niq'}
                        </p>
                        <p className="text-sm text-[var(--text-secondary)] mt-1">
                            {settings?.siteDescription || 'Digital Artist & Illustrator'}
                        </p>
                    </div>

                    {/* Navigation */}
                    <nav className="flex flex-wrap justify-center gap-6">
                        {['Portfolio', 'Commissions', 'Process', 'Posts', 'Contact'].map((item) => (
                            <Link
                                key={item}
                                href={`/${item.toLowerCase()}`}
                                className="text-sm text-[var(--text-secondary)] hover:text-[var(--accent-primary)] transition-colors font-medium"
                            >
                                {item}
                            </Link>
                        ))}
                    </nav>

                    {/* Social links */}
                    <div className="flex justify-center md:justify-end gap-4">
                        {settings?.socialLinks?.filter(link => link.url && link.url !== '#').map((link, index) => {
                            const IconComp = socialIcons[link.platform] || socialIcons.Default;
                            return (
                                <a
                                    key={index}
                                    href={link.url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="w-10 h-10 rounded-full bg-[var(--bg-tertiary)] flex items-center justify-center text-[var(--text-secondary)] hover:text-white hover:bg-[var(--accent-primary)] transition-all duration-300"
                                    title={link.platform}
                                >
                                    <IconComp className="w-5 h-5" />
                                </a>
                            );
                        })}
                    </div>
                </div>

                <div className="mt-12 pt-8 border-t border-[var(--border-primary)] flex flex-col sm:flex-row justify-between items-center gap-4">
                    <p className="text-sm text-[var(--text-tertiary)] text-center">
                        ¬© {new Date().getFullYear()} {settings?.siteTitle || 'Miqk Niq'} - All Rights Reserved
                    </p>
                    <div className="flex gap-6">
                        <Link href="/privacy" className="text-xs text-[var(--text-tertiary)] hover:text-[var(--text-secondary)]">Privacy Policy</Link>
                        <Link href="/terms" className="text-xs text-[var(--text-tertiary)] hover:text-[var(--text-secondary)]">Terms of Service</Link>
                    </div>
                </div>
            </div>
        </footer>
    );
}
